.about__about-container{

    background-size: 1920px;
    background-position: center;

}

.about__about-main{

    background-color: rgba(48, 48, 48, 0.541);

}

.about__img{

    border: 2px solid lightseagreen;
    border-radius: 100%;

}

.about__title-line{

    background-color: lightseagreen;
    height: 1px;

}

.about__main{

    overflow: scroll;
    scroll-behavior: smooth;

}

.about__main-text{

    text-align: center;
    background-color: rgba(48, 48, 48, 0.459);
    border-radius: 60px;
    
}

.about__text{

    overflow: scroll;
    scroll-behavior: smooth;

}

::-webkit-scrollbar{
    display: none;
}


@media(max-width: 1250px){

    .about__about-container{

        background-size: 1250px;

    }

    .about__about-main{

        overflow: scroll;
        scroll-behavior: smooth;
        
    }

}

