.contact__contact-container{

    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;

}

.contact__contact-black{

    background-color: rgba(49, 49, 49, 0.637);

}

.contact__title-line{

    margin-top: 3vh;
    background-color: lightseagreen;
    height: 1px;
    width: 40%;

}


.contact__icon{

    color: lightseagreen;
    cursor: pointer;
    font-size: 5vw;
    transition: color .2s;

}

.box-active{
    border: 1px solid lightseagreen;
}

.in{
    &:hover{
        color: #0e76a8;
    }
}

.outlook {
    &:hover{
        color: #3399ff;
    }
}

.git {
    &:hover{
        color: #d8d8d8;
    }
}


@media (max-width: 700px){
    
    .contact__icon{
        font-size: 10vw;
    }

}