.skills___skills-router{

    background-position: center;

}

.skills__skills-black{

    background-color: rgba(43, 39, 39, 0.692);

}

.skills__title-cointeiner{

    height: 40%;

}

.skills__title-line{

    margin-top: 3vh;
    background-color: lightseagreen;
    height: 1px;
    width: 40%;

}

.skills__icons-container{

    height: 60%;

}

.skills__icon{

    color: lightseagreen;
    cursor: pointer;
    font-size: 50px;
    transition: color .5s;

}

.skills__back-icon{
    transition: color .3s;
    &:hover{
        color: lighten($color: lightseagreen, $amount: 10);
    }
}

.javascript {

    &:hover{
        color: #F0DB4F;
    }

}

.html {

    &:hover{
        color: #f06529;
    }

}

.css {

    &:hover{
        color: #264de4;
    }

}

.react{

    &:hover{
        color: #61DBFB;
    }

}

.sass {

    &:hover{
        color: #CD6799;
    }

}

.caruselo-test{
    height: 100%;
    width: 100%;
}