.body{

    background-color: #221f1f;
    color: lightseagreen;
    font-family: 'Roboto Condensed', sans-serif;
    height: 100vh;
    width: 100vw;

}

.body__line{

    margin-top: 15vh;
    height: 1px;
    width: 50vw;
    transition: width .3s;

}

.body__window-container{

    height: 50vh;
    margin-top: 15vh;
    width: 85vw;

}

.body__window-box{
    
    height: 100%;
    overflow: hidden;
    position: relative;
    width: 100%;
    z-index: 99;
    
}

.body__solid-box{

    background-color: #221f1f;
    border-radius: 5px;
    height: 50vh;
    left: 99.5%;
    position: absolute;
    transition: .3s;
    width: 85vw;
    z-index: 0;

}

.body__solid-box-line{

    background-color: lightseagreen;
    border-radius: 5px 0px 0px 5px;
    height: 0%;
    transition: height .2s;
    width: 0.5%;

}

.solidAction{
    left: 0%;
}

.lineAction{
    height: 100%;
}