.home__home-container {

    background-position: center;

}

.home__home-main{

    background-color: rgba(37, 37, 37, 0.699);
    font-family: 'Oswald', sans-serif;

}

.home__home-logo{
    
    cursor: pointer;
    transition: width .3s;
    width: 200px;

}

@media(max-width: 1250px){
    .home__home-container{

        background-size: 1250px;

    }

}

@media (max-width: 800px){

    .home__home-main{

        justify-content: center;

    }

    .home__text{

        display: none;

    }

}